@-ms-viewport {
    width: device-width;
}
* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-smoothing: antialiased;
    -webkit-text-rendering: optimizeSpeed;
    text-rendering: optimizeSpeed;

    box-sizing: border-box;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;

    font-kerning: none;

    --heightButton: 44px;
    --borderButton: 2px;
}
html {
    font-size: 62.5%;
}
body {
    width: 100%;
    max-width: $container-max-width;
    margin: auto;
    @include text($fs-text-s, $font-main, $lh-text-s, $text-cl, none);
    background: $white-cl;

    --leftWrapper: 300px;
    .--purple-cl {
        color: $purple-cl;
    }
    .wrapper {
        background: $main-bg-cl;
        position: relative;
        z-index: 10;
        @include transform(translate(0%, 0px) scale(1));
        @include transition(0.45s cubic-bezier(0.63, 0.16, 0.22, 0.94));
        &.--show-nav {
            @include transform(translate(calc(-100% + var(--leftWrapper)), -30px) scale(0.7));
            @include transition(0.45s cubic-bezier(0.63, 0.16, 0.22, 0.94));
        }
        &.--overflow-hidden {
            height: 100vh;
            overflow: hidden;
        }
    }
    .progressbar {
        height: 4px;
        background-color: $bluedark-bg-cl;
        position: fixed;
        top: 0;
        max-width: $container-max-width;
        z-index: 800;
        @include transition(0.4s ease-out);
    }
    .container {
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
        margin-right: auto;
        margin-left: auto;
        position: relative;
        z-index: 10;
        &.--small {
            max-width: 1060px;
        }
    }
    .container-fluid {
        width: 100%;
        padding-right: 30px;
        padding-left: 30px;
        margin-right: auto;
        margin-left: auto;
        @include xs {
            padding-right: 15px;
            padding-left: 15px;
        }
    }
    .row {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        margin-right: -15px;
        margin-left: -15px;
    }
    .no-gutters {
        margin-right: 0;
        margin-left: 0;
    }
    .no-gap > .col,
    .no-gap > [class*="col-"] {
        padding-right: 0;
        padding-left: 0;
    }

    @media (min-width: $xxs-min) {
        .container {
            max-width: 540px;
        }
    }
    @media (min-width: $xs-min) {
        .container {
            max-width: 720px;
        }
    }
    @media (min-width: $sm-min) {
        .container {
            max-width: 960px;
            &.--small {
                max-width: 960px;
            }
        }
    }
    @media (min-width: $md-min) {
        .container {
            max-width: $container-width;
        }
    }

    .mainwrapper {
        width: 100%;
        height: 100%;
        margin: auto;
        @include visible;
        overflow: hidden;
        //will-change: transform;
        .--ptop {
            padding-top: 160px;
        }
        .pin-spacer {
            //will-change: transform;
        }
    }
    @include lg {
        // CSS responsive large desktop
    }
    @include md {
        // CSS responsive desktop
    }
    @include sm {
        // CSS responsive tablet
    }
    @include xs {
        --leftWrapper: 110px;
        // CSS responsive mobile
        .mainwrapper {
            .--ptop {
                padding-top: 30px;
            }
        }
    }
    @include xxs {
        // CSS responsive small mobile
    }

    @media (max-width: 500px) {
    }

    @include maxW(500px) {
    }
}
