.loginpage {
    height: 100vh !important;
    background: $blue-bg-cl;
    --heightInput: 44px;
    .container {
        height: 100%;
        @include mid(true);
    }
    .formwrapper {
        height: 673px;
        .loginform {
            width: 434px;
            text-align: center;
            padding: 40px;
            border-radius: 10px;
            background: $main-bg-cl;
            font-size: $fs-text-s;
            .title {
                font-family: pm;
                color: $text-blue-cl;
                margin-top: 5px;
                margin-bottom: 5px;
            }
            .btnsocial {
                display: grid;
                grid-template-columns: 1fr 1fr;
                column-gap: 18px;
                margin: 20px 0;
                .btnsc {
                    height: var(--heightInput);
                    @include mid(true);
                    border-radius: 5px;
                    color: $text-blue-cl;
                    font-family: pm;
                    i {
                        display: inline-block;
                        margin-right: 10px;
                        margin-bottom: -3px;
                    }
                    &.--google {
                        border: 2px solid #e1e8f1;
                        @include transition($t);
                        &:hover {
                            background: #f6f1fd;
                            @include transition($t);
                        }
                    }
                    &.--facebook {
                        background: #4267b2;
                        color: $text-white-cl;
                        @include transition($t);
                        &:hover {
                            background: #34579d;
                            @include transition($t);
                        }
                    }
                }
            }
            .line {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 20px;
                &:before,
                &:after {
                    content: "";
                    width: 50%;
                    height: 1px;
                    background: #e7e7e7;
                }
                span {
                    white-space: nowrap;
                    margin: 0 5px;
                    color: $text-grey-cl;
                    opacity: 0.7;
                    margin-top: -2px;
                }
            }
            .remember {
                display: flex;
                align-items: center;
                justify-content: space-between;
                a {
                    @include gradient;
                    @include transition($t);
                    &:hover {
                        opacity: 0.6;
                        @include transition($t);
                    }
                }
            }
            .btn--submit {
                margin: 25px 0;
            }
            .textsignup {
                a {
                    @include gradient;
                    margin-top: 6px;
                    display: block;
                    @include transition($t);
                    &:hover {
                        opacity: 0.6;
                        @include transition($t);
                    }
                    &:focus {
                        opacity: 1;
                    }
                }
            }
        }
        .texthomepage {
            text-align: center;
            display: table;
            margin: 30px auto;
            color: $text-cl;
            @include transition($t);
            i {
                margin-right: 10px;
                position: relative;
                bottom: -2px;
            }
            &:hover {
                color: $text-blue-cl;
                @include transition($t);
            }
        }
    }
    @include xs {
        background: $main-bg-cl;
        overflow-y: scroll;
        .container {
            overflow-y: scroll;
        }
        .formwrapper {
            width: 100%;
            overflow-y: scroll;
            display: flex;
            flex-direction: column-reverse;
            margin-top: -50px;
            .loginform {
                width: 100%;
                margin: 0 auto;
                padding: 20px 0;
                .btnsocial {
                    column-gap: 14px;
                }
            }
            .texthomepage {
                margin: 15px auto;
            }
        }
    }
    @include xsX {
        .formwrapper {
            margin-top: 0;
        }
    }
}
