// css components
.btngroup {
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    .btn {
        width: 210px;
        margin: 0 8px;
        font-size: $fs-text-s;
        &.--big {
            height: 54px;
        }
    }
    &.--no-gutters {
        .btn {
            margin: 0;
        }
    }
    &.--center {
        margin: 0 auto;
    }
    @include xs {
        flex-direction: column;
        .btn {
            width: 100%;
            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }
    }
}

.modal {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 900;
    opacity: 0;
    pointer-events: none;
    @include transition($t);
    background: rgba($color: #000000, $alpha: 0.95);
    --heightInput: 44px;
    &__close {
        position: absolute;
        right: 10px;
        top: 10px;
        cursor: pointer;
        @include transition($t);
        &:hover {
            opacity: 0.7;
            @include transition($t);
        }
    }
    &__inner {
        @include mid;
        .title {
            font-family: pm;
            color: $text-cl;
            font-family: psb;
            margin-top: 5px;
            margin-bottom: 5px;
        }
        .subtitle {
            margin: 8px 0 20px 0;
            color: $text-grey-cl;
            opacity: 0.8;
        }
        &.modalsignup {
            width: 400px;
            background: #fff;
            text-align: left;
            padding: 40px;
            border-radius: 10px;
            background: $main-bg-cl;
            font-size: $fs-text-s;
            opacity: 0;
            pointer-events: none;
            @include transition($t);
            &.active {
                opacity: 1;
                pointer-events: auto;
                @include transition($t);
            }
        }
        &.modalsuccess {
            width: 400px;
            background: #fff;
            text-align: left;
            padding: 40px;
            border-radius: 10px;
            background: $main-bg-cl;
            font-size: $fs-text-s;
            opacity: 0;
            pointer-events: none;
            @include transition($t);
            .notisuccess {
                text-align: center;
                p {
                    margin-bottom: 0;
                }
            }
            &.active {
                opacity: 1;
                pointer-events: auto;
                @include transition($t);
            }
        }
        .form {
            &.hide {
                display: none;
            }
            .btn--submit {
                margin: 25px 0 0 0;
            }
        }
    }

    &.active {
        opacity: 1;
        pointer-events: auto;
        @include transition($t);
    }
    @include xs {
        &__inner {
            &.modalsignup,
            &.modalsuccess {
                width: calc(100% - 40px);
                max-width: 400px;
                padding: 40px 20px;
            } 
        }
    }
}

//////////////////////////// INPUT //////////////////////////
.form__group {
    &:not(:last-child) {
        margin-bottom: 15px;
    }
}
.inputform {
    width: 100%;
    height: auto;
    position: relative;
    @include transition($t);
    i {
        z-index: 1;
        position: absolute;
        left: 15px;
        top: 27px;
        @include transform(translateY(-50%));
        &:first-child {
            top: 29px;
        }
    }
    br {
        display: none;
    }
    input {
        width: 100%;
        height: 54px;
        background: #ffffff;
        border: 1px solid #d0d0d0;
        box-sizing: border-box;
        border-radius: 5px;
        font-family: pr;
        font-size: $fs-text-s;
        outline: 0;
        padding: 5px 15px 5px 38px;
        @include transition($t);
        &:focus {
            border: 1px solid $bluedark-bg-cl;
            box-shadow: 0px 0px 0px 3px rgba(0, 0, 0, 0.05);
            @include transition($t);
        }
    }
    .wpcf7-not-valid-tip {
        color: $orange-bg-cl;
        font-size: $fs-text-ss;
        line-height: $fs-text-ss;
        margin-top: 5px;
        text-align: right;
    }
    .iconeye {
        width: 20px;
        height: 20px;
        cursor: pointer;
        position: absolute;
        right: 15px;
        left: initial;
    }
    &.error {
        input {
            border: 1px solid $orange-bg-cl;
            @include transition($t);
        }
    }
}

.errorform {
    display: none;
    color: $orange-bg-cl;
    font-size: $fs-text-ss;
    line-height: $fs-text-ss;
    margin-top: 5px;
    text-align: right;
}

.wpcf7-not-valid-tip {
    color: $orange-bg-cl;
    font-size: $fs-text-ss;
    line-height: $fs-text-ss;
    margin-top: 5px;
    text-align: right;
}

.modal .wpcf7-submit {
    background: transparent;
    margin: 0px !important;
    width: 100%;
    height: 54px;
    padding: 10px 20px;
    line-height: initial;
    display: block;
    overflow: hidden;
    color: $white-cl;
    font-family: pm;
    text-align: center;
    @include mid(true);
    position: relative;
    z-index: 10;
    border: none;
    cursor: pointer;
    @include transition($t);    
}

.modal .wpcf7 .ajax-loader,
.modal .wpcf7-response-output {
    display: none;
}
