// CSS HOMEPAGE
.homepage {
    .scmain {
        &:after {
            background: $black-bg-cl;
            content: "";
            width: 100%;
            height: 300px;
            bottom: -2px;
            left: 0;
            position: absolute;
            z-index: 2;
        }
        @include xs {
            &:after {
                height: 115px;
            }
        }
    }
}
.scmain {
    min-height: 865px;
    position: relative;
    background: $main-bg-cl;
    &:before {
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        z-index: 0;
        $bg-color: $main-bg-cl;
        $dot-color: #29349f;
        $dot-size: 2px;
        $dot-space: 38px;
        background: linear-gradient(90deg, $bg-color ($dot-space - $dot-size), transparent 1%) center,
            linear-gradient($bg-color ($dot-space - $dot-size), transparent 1%) center, $dot-color;
        background-size: $dot-space $dot-space;
        opacity: 0.2;
        pointer-events: none;
    }
    .container {
        position: relative;
        z-index: 10;
    }
    &__textbox {
        text-align: center;
        max-width: 900px;
        margin: auto;
        .label {
            margin-bottom: 12px;
            display: block;
        }
        .title {
            margin: 0 auto 32px auto;
            max-width: 700px;
        }
        &-checklist {
            display: inline-flex;
            margin: 0 auto;
            li {
                margin: 0 35px;
                @include mid(true);
                i {
                    width: 30px;
                    height: 30px;
                    flex-shrink: 0;
                    background: $green-bg;
                    @include br(50%);
                    @include mid(true);
                    margin-right: 12px;
                }
            }
        }
    }

    &__form {
        max-width: 210px;
        display: block;
        align-items: center;
        margin: 10px auto 40px auto;
        .form__input {
            position: relative;
            &.error {
                input {
                    border: 1px solid $orange-bg-cl;
                    @include transition($t);
                }
            }
        }
        input {
            width: 305px;
            height: calc(var(--heightButton) + 10px);
            background: $white-cl;
            border-radius: 5px;
            outline: 0;
            border: 1px solid #d0d0d0;
            margin-right: 10px;
            padding: 14px 16px;
            font-size: $fs-text;
            @include transition($t);
            &:focus {
                border: 1px solid $bluedark-bg-cl;
                box-shadow: 0px 0px 0px 3px rgba(0, 0, 0, 0.05);
                @include transition($t);
            }
        }
        .errorform {
            display: none;
            position: absolute;
            right: 10px;
            opacity: 0;
        }
        .wpcf7-form {
            display: inline-flex;
        }
        .wpcf7-email {
            background: $white-cl !important;
        }
        .wpcf7-not-valid-tip {
            color: $orange-bg-cl;
            font-size: $fs-text-ss;
            line-height: $fs-text-ss;
            margin-top: 5px;
            margin-right: 10px;
            text-align: right;
        }
        .btn {
            height: 54px;
            .wpcf7-submit {
                width: auto;
                background: transparent;
                margin: 0px !important;
                padding: 10px 24px;
                line-height: initial;
                display: block;
                overflow: hidden;
                color: $white-cl;
                font-family: pm;
                text-align: center;
                @include mid(true);
                position: relative;
                z-index: 10;
                border: none;
                cursor: pointer;
                @include transition($t);    
            }
        }
        .wpcf7 .ajax-loader,
        .wpcf7-response-output {
            display: none;
        }
    }
    &__video {
        margin: 84px auto 0 auto;
        display: table;
        filter: drop-shadow(0px 4px 20px rgba(70, 101, 181, 0.05));
        position: relative;
        // top: -310px;
        overflow: hidden;
        //border: 5px solid rgba(255, 255, 255, 0.2);
        border-radius: 8px;
        img,
        video {
            display: block;
            max-width: 966px;
            width: 100%;
        }
    }
    @include xs {
        
        &__textbox {
            text-align: left;
            .bottom {
                width: 100%;
                display: inline-flex;
                flex-direction: column-reverse;
            }
            &-checklist {
                width: 100%;
                flex-direction: column;
                li {
                    justify-content: flex-start;
                    margin: 0;
                    &:not(:last-child) {
                        margin: 0 0 15px 0;
                    }
                    i {
                        width: 24px;
                        height: 24px;
                        img {
                            width: 11px;
                        }
                    }
                }
            }
        }

        &__form {
            width: 100%;
            flex-direction: column;
            display: flex;
            margin: 30px auto;
            max-width: none;
            .form__input {
                width: 100%;
            }
            input {
                width: 100%;
                margin-right: 0;
                margin-bottom: 10px;
                height: calc(var(--heightButton) + 6px);
            }
            .errorform {
                position: relative;
                right: 0px;
                margin-bottom: 10px;
                margin-top: 0;
            }
            .btn {
                width: 100%;
            }
        }
        &__video {
            margin: 45px auto 0 auto;
        }
    }
}
.scprosol {
    height: 100vh;
    position: relative;
    z-index: 10;
    background: $black-bg-cl;
    padding: $pd-section 0;
    --heightContent: 700px;
    overflow: hidden;
    display: flex;
    align-items: center;
    @include transition(background 0.5s);
    &.--fixed {
        width: 100%;
        top: 0;
        left: 0;
        position: fixed;
    }
    &:after {
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        z-index: 0;
        $bg-color: $black-bg-cl;
        $dot-color: #242424;
        $dot-size: 2px;
        $dot-space: 38px;
        background: linear-gradient(90deg, $bg-color ($dot-space - $dot-size), transparent 1%) center,
            linear-gradient($bg-color ($dot-space - $dot-size), transparent 1%) center, $dot-color;
        background-size: $dot-space $dot-space;
        pointer-events: none;
        display: none;
    }
    .container {
        position: relative;
        z-index: 10;
        display: grid;
        grid-template-columns: 1fr 700px;
    }
    &__content {
        height: var(--heightContent);
        position: relative;
        overflow: hidden;
        z-index: 1;
        &-wrapper {
            max-width: 420px;
            position: relative;
            &:before,
            &:after {
                content: "";
                width: 100%;
                height: 300px;
                position: absolute;
                left: 0;
                background: linear-gradient(to top, $black-bg-cl, rgba($black-bg-cl, 0));
                z-index: 40;
            }
            &::before {
                top: -3px;
                background: linear-gradient(to bottom, $black-bg-cl, rgba($black-bg-cl, 0));
            }
            &::after {
                bottom: -3px;
            }
        }
        &-inner {
            position: relative;
            top: 250px;
            //will-change: transform;
        }
        .des {
            color: $text-white-cl;
            opacity: 0.7;
            font-size: $fs-text;
            line-height: $lh-text;
        }
        &-item {
            &:not(:last-child) {
                margin-bottom: 90px;
            }
        }
    }
    &__circle {
        position: relative;
        height: var(--heightContent);
        z-index: 10;
        &-item {
            width: 110%;
            height: 110%;
            margin: -30px auto;
            opacity: 0;
            @include mid;
            &.active {
                opacity: 1;
            }
            .lottie-wrap {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                margin: 0 auto;
            }
            lottie-player {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                margin: 0 auto;
                svg {
                    width: 100%;
                    height: 100%;
                }
            }
            .mask {
                border-radius: 200px;
                overflow: hidden;
                width: 160px;
                height: 160px;
                position: absolute;
                top: calc(50% - 80px);
                left: calc(50% - 80px);
                @media not all and (min-resolution: 0.001dpcm) {
                    top: calc(50% - 110px);
                }
                video {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
    @include md {
        --heightContent: 420px;
        &__content {
            width: 400px;
            height: 690px;
            max-width: 600px;
            margin: auto;
        }
        &__circle {
            margin-top: 120px;
            .mask {
                @include size(80px);
                top: calc(50% - 35px);
                left: calc(50% - 40px);
            }
        }
    }
    @include sm {
        padding: 50px 0;
        height: auto;
        --heightContent: 280px;
        .container {
            grid-template-columns: 1fr;
        }
        &__content {
            max-width: initial;
            width: 100%;
            height: var(--heightContent);
            overflow: initial;
            .title {
                font-size: 2.4rem;
                line-height: 3.2rem;
            }
            &-wrapper {
                order: 2;
                &::after {
                    display: none;
                }
                &:before {
                    width: 100px;
                    height: 100%;
                    right: -15px;
                    left: initial;
                    background: linear-gradient(to left, $black-bg-cl, rgba($black-bg-cl, 0));
                    display: none;
                    z-index: 100;
                    @include maxW(580px) {
                        display: block;
                    }
                }
            }
            &-inner {
                top: 0;
                margin-left: -15px;
                margin-right: -15px;
                padding: 0 15px;
                .flickity-viewport {
                    overflow: initial;
                }
            }

            &-item {
                width: 90%;
                padding-right: 40px;
                position: relative;
                z-index: 10;
                &:not(:last-child) {
                    margin-bottom: 00px;
                }
            }
        }
        &__circle {
            height: 340px;
            margin-top: 0;
            &-item {
                .mask {
                    @include size(80px);
                    top: calc(50% - 35px);
                    left: calc(50% - 40px);
                }
            }
        }
    }
}
.scworks {
    padding: 150px 0;
    position: relative;
    &__steps {
        &-textbox {
            max-width: 550px;
            text-align: center;
            margin: 0 auto;
            .label {
                display: inline-block;
                // span {
                //     @include gradient;
                // }
            }
        }
        &-list {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            margin-top: 60px;
            .item {
                padding: 20px 30px;
                text-align: center;
                &__number {
                    opacity: 0.6;
                    margin: 0;
                    display: block;
                    flex-shrink: 0;
                    display: inline-block;
                    @include gradient;
                    // span {
                    //     @include gradient;
                    // }
                }
                h4 {
                    margin: 4px 0px;
                    font-size: $fs-text;
                    line-height: $lh-text;
                }
                .des {
                    font-size: $fs-text-s;
                    line-height: $lh-text;
                    .whitespace {
                        display: inline-block;
                        width: 3.9px;
                        height: $lh-text;
                    }
                }
            }
        }
    }
    &__text {
        text-align: center;
        max-width: 850px;
        margin: 0 auto;
        padding-top: 150px;
        padding-bottom: 30px;
        &-title {
            margin: auto;
            &.--gradient {
                line-height: 7.8rem;
            }
        }
        &-list {
            width: 100%;
            height: 90px;
            position: relative;
            text-align: center;
            overflow: hidden;
            margin-top: 8px;
            .inner {
                height: 100%;
                //opacity: 0;
                display: grid;
                grid-template-columns: 1fr;
                //@include transform(translateY(120px));
            }
            .title {
                grid-row: 1/2;
                grid-column: 1/2;
                height: 90px;
                white-space: nowrap;
                pointer-events: none;
                //margin-top: -2px;
                @include gradient;
                display: inline-block;
                .word {
                    overflow: visible;
                }
                // .char {
                //     height: 90px;
                //     @include gradient;
                // }
            }
        }
    }
    &__bg {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        .container {
            width: 100%;
            height: 100%;
            position: relative;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }
        span {
            width: 100%;
            height: 100%;
            border-right: 1px dashed rgba(0, 0, 0, 0.1);
            &:first-child {
                border-left: 1px dashed rgba(0, 0, 0, 0.1);
            }
        }
    }
    @include sm {
        &__steps {
            &-list {
                grid-template-columns: repeat(2, 1fr);
                .item:nth-child(3) {
                    grid-column: 1/3;
                    width: 50%;
                    margin: 0 auto;
                }
            }
        }
    }
    @include xs {
        padding: 60px 0;
        &__steps {
            &-textbox {
                text-align: left;
            }
            &-list {
                margin-top: 30px;
                grid-template-columns: 1fr;
                .item {
                    padding: 0;
                    display: flex;
                    text-align: left;
                    &__number {
                        width: 76px;
                    }
                    &:not(:last-child) {
                        .item__text {
                            margin-bottom: 10px;
                            padding-bottom: 13px;
                            border-bottom: 1px solid #e6e7ea;
                        }
                    }
                    &:nth-child(3) {
                        grid-column: 1/2;
                        width: 100%;
                        margin: unset;
                    }
                }
            }
        }
        &__bg {
            display: none;
        }
        &__text {
            padding-top: 60px;
            padding-bottom: 0px;
            .title {
                &.--t0 {
                    font-size: 3.6rem;
                    line-height: 4.4rem;
                }
            }
            &-list {
                height: 52px;
                .title {
                    height: 52px;
                    .char {
                        height: 52px;
                    }
                }
            }
        }
    }
}
.sccollab {
    height: 1160px;
    --wCol: 270px;
    --pdCol: 30px;
    &__inner {
        display: grid;
        grid-template-columns: 368px 1fr;
        grid-column-gap: 32px;
        background: url(../img/bg-collab.svg) no-repeat top center;
        background-size: contain;
        padding: 0 100px;
    }
    &__textbox {
        padding: 130px 0 50px;
        .title {
            &:first-child {
                display: block;
            }
            &:last-child {
                display: none;
            }
        }
    }
    &__boxs {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 32px;
        margin-top: 0px;
        justify-self: flex-end;
        .item {
            width: 100%;
            padding: 44px 33px 40px 40px;
            background: $main-bg-cl;
            @include br(5px);
            &:not(:last-child) {
                margin-bottom: var(--pdCol);
            }
            h5 {
                margin: 12px 0;
                font-family: pm;
                font-size: 1.6rem;
                line-height: 2.6rem;
            }
            &.--item-cta {
                background: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);
                padding: 44px 40px 40px 40px;
                .title {
                    margin-top: 0;
                }
                .btn {
                    &.--desktop {
                        display: block;
                        height: 54px;
                        a {
                            height: 100%;
                        }
                    }
                    &.--mobile {
                        display: none;
                    }
                }
                p {
                    color: $text-white-cl;
                    opacity: 0.7;
                    margin-top: 20px;
                    text-align: center;
                }
            }
        }
        &-col {
            padding-top: 110px;
            &:first-child {
                padding-top: 300px;
            }
        }
    }
    @include md {
        &__inner {
            grid-template-columns: 280px 1fr;
            padding: 0 40px;
        }
        &__textbox {
            padding: 120px 50px 50px 0px;
        }
        &__boxs {
            gap: 20px;
            &-col {
                padding-top: 80px;
                &:first-child {
                    padding-top: 260px;
                }
            }
        }
    }
    @include sm {
        height: auto;
        &__inner {
            background: none;
            padding: 20px 15px 20px 15px;
            grid-template-columns: 1fr;
            position: relative;
            &:after {
                content: "";
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                background: linear-gradient(
                    313.04deg,
                    rgba(54, 120, 247, 0.1) 3.19%,
                    rgba(167, 112, 239, 0.1) 100%,
                    rgba(167, 112, 239, 0.1) 100%
                );
                border-bottom-left-radius: 220px;
                border-bottom-right-radius: 220px;

                z-index: 1;
            }
        }
        &__textbox {
            text-align: center;
            padding: 20px 0;
            width: 100%;
            position: relative;
            z-index: 10;
            .title {
                &:first-child {
                    display: none;
                }
                &:last-child {
                    display: block;
                }
            }
        }
        &__boxs {
            grid-template-columns: 1fr;
            gap: 15px;
            position: relative;
            z-index: 10;
            .item {
                text-align: center;
                padding: 25px 30px;
                &:not(:last-child) {
                    margin-bottom: 15px;
                }
                &.--item-cta {
                    background: none;
                    padding-top: 15px;
                    padding-bottom: 45px;
                    .title {
                        max-width: 260px;
                        text-align: center;
                        margin: 0 auto;
                        &.--white {
                            color: $text-cl;
                        }
                    }
                    .btn {
                        max-width: 237px;
                        margin: 15px auto 0 auto;
                        &.--desktop {
                            display: none;
                        }
                        &.--mobile {
                            display: block;
                            max-width: 237px;
                        }
                    }
                    p {
                        color: $text-grey-cl;
                    }
                }
            }
            &-col {
                padding-top: 0;
                &:first-child {
                    padding-top: 0;
                }
            }
        }
    }
}
.scfeatured {
    padding: 300px 0 80px 0;
    &__row {
        display: flex;
        justify-content: space-between;
        &-img {
            position: relative;
            img {
                opacity: 1;
            }            
            .img-show {
                opacity: 1;
                position: absolute;
                top: -25%;
                left: 0;
                max-width: 100%;
            }
        }
        &-text {
            max-width: 500px;
            padding: 0 50px;
            margin-right: 40px;
            .title {
                margin-top: -5px;
                margin-bottom: 30px;
            }
            ul {
                li {
                    position: relative;
                    padding-left: 28px;
                    &:before {
                        content: "";
                        width: 14px;
                        height: 12px;
                        background: url(../img/iconchecked.svg) no-repeat center;
                        background-size: contain;
                        position: absolute;
                        top: 6px;
                        left: 0;
                    }
                    h6 {
                        margin-top: 0;
                        margin-bottom: 5px;
                        font-size: $fs-text;
                        line-height: $lh-text;
                    }
                    &:not(:last-child) {
                        margin-bottom: 15px;
                    }
                }
            }
        }
        &:not(:last-child) {
            margin-bottom: 140px;
        }
        &:nth-child(2) {
            flex-direction: row-reverse;
        }
    }
    &__btn {
        margin-top: 140px;
    }
    @include sm {
        padding: 60px 0;
        &__row {
            flex-direction: column-reverse;
            &:not(:last-child) {
                margin-bottom: 60px;
            }
            &:nth-child(even) {
                flex-direction: column-reverse;
            }
            // &-img {
            // }
            &-text {
                max-width: initial;
                padding: 0px;
                margin-right: 0px;
                margin-bottom: 30px;
            }
        }
        &__btn {
            margin-top: 40px;
        }
    }
}
.sctestimonials {
    --gapCol: 100px;
    --timeProcessing: 6s;
    --timeFade: 1s;
    padding: 80px 0;
    &__slider {
        background: $black-bg-cl;
        padding: 80px 100px;
        @include br(10px);
        .slide {
            color: $text-white-cl;
            &__top {
                display: grid;
                grid-template-columns: 1fr 1fr;
                column-gap: var(--gapCol);
                &-title {
                    .title {
                        margin-top: 0;
                        margin-bottom: 0;
                    }
                }
                &-user {
                    display: flex;
                    align-items: flex-end;
                    justify-content: space-between;
                    padding-bottom: 0px;
                    .userlist {
                        height: 130px;
                        position: relative;
                        .user {
                            position: absolute;
                            top: 0;
                            left: 0;
                            opacity: 0;
                            @include transition(var(--timeFade));
                            .img {
                                @include size(68px);
                                @include br(10px);
                                overflow: hidden;
                                position: relative;
                                margin-bottom: 15px;
                                img {
                                    @include midimg(100%);
                                }
                            }
                            .name {
                                white-space: nowrap;
                                font-size: $fs-text;
                                line-height: $lh-text;
                            }
                            .position {
                                white-space: nowrap;
                                opacity: 0.8;
                            }
                            &.active {
                                opacity: 1;
                                @include transition(var(--timeFade));
                            }
                        }
                    }
                    .controls {
                        display: flex;
                        align-items: center;
                        &__btn {
                            @include size(48px);
                            @include br(50%);
                            @include mid(true);
                            position: relative;
                            cursor: pointer;
                            &:after {
                                content: "";
                                width: 100%;
                                height: 100%;
                                background-color: $main-bg-cl;
                                opacity: 0;
                                top: 0;
                                left: 0;
                                position: absolute;
                                @include br(50%);
                                @include transition($t);
                            }
                            img,
                            svg {
                                opacity: 0.3;
                                @include transition($t);
                            }
                            &:first-child {
                                img,
                                svg {
                                    @include transform(rotate(180deg) translateX(2px));
                                }
                            }
                            &:hover {
                                &::after {
                                    opacity: 0.1;
                                    @include transition($t);
                                }
                                img,
                                svg {
                                    opacity: 1;
                                    @include transition($t);
                                }
                            }
                        }
                    }
                }
            }
            &__timeline {
                width: 100%;
                margin: 50px 0;
                position: relative;
                ul {
                    width: 100%;
                    display: flex;
                    li {
                        width: calc(100% / 4);
                        height: 3px;
                        background: rgb(41, 41, 41);
                        margin: 0 1px;
                        cursor: pointer;
                        position: relative;
                        @include transform(scaleY(1));
                        @include transition(0.3s);
                        @include keyframes(processing) {
                            from {
                                width: 0%;
                            }
                            to {
                                width: 100%;
                            }
                        }
                        span {
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            top: 0;
                            left: 0;
                            background: $bluedark-bg-cl;
                            display: block;
                            opacity: 0;
                            transition: opacity 0.5s;
                        }
                        &.active {
                            span {
                                opacity: 1;
                                animation: processing var(--timeProcessing) linear;
                                transition: opacity 0.5s;
                            }
                        }
                        &:hover {
                            background: rgb(75, 75, 75);
                            @include transform(scaleY(2));
                            @include transition(0.3s);
                        }
                        @include sm {
                            &:focus,
                            &:hover {
                                background: rgb(41, 41, 41);
                                @include transform(scaleY(1));
                            }
                        }
                    }
                }
                &-processing {
                    width: 0%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: block;
                    background: $bluedark-bg-cl;
                    z-index: 10;
                }
            }
            &__bottom {
                display: grid;
                grid-template-columns: 1fr 1fr;
                column-gap: var(--gapCol);
                &-title {
                    max-width: 320px;
                    display: flex;
                    span {
                        flex-shrink: 0;
                        margin-right: 15px;
                    }
                    .titlelist {
                        width: 100%;
                        position: relative;
                        &__item {
                            position: absolute;
                            top: 0;
                            opacity: 0;
                            @include transition(var(--timeFade));
                            &.active {
                                opacity: 1;
                                @include transition(var(--timeFade));
                            }
                        }
                    }
                }
                &-content {
                    min-height: 200px;
                    position: relative;
                    opacity: 0.8;
                    .pitem {
                        position: absolute;
                        top: 0;
                        opacity: 0;
                        @include transition(var(--timeFade));
                        &.active {
                            opacity: 1;
                            @include transition(var(--timeFade));
                        }
                    }
                }
            }
        }
    }
    @include sm {
        padding: 50px 0;
        background: $black-bg-cl;
        &__slider {
            padding: 0;
            background: none;
            .slide {
                &__top {
                    grid-template-columns: 1fr;
                    &-user {
                        margin-top: 35px;
                        .userlist {
                            width: 100%;
                            height: 70px;
                            .user {
                                width: 100%;
                                display: flex;
                                align-items: center;
                                .img {
                                    margin-bottom: 0;
                                    margin-right: 15px;
                                }
                            }
                        }
                        .controls {
                            display: none;
                        }
                    }
                }
                &__timeline {
                    margin: 32px 0;
                }
                &__bottom {
                    grid-template-columns: 1fr;
                    &-title {
                        max-width: 100%;
                        flex-direction: row-reverse;
                        position: relative;
                        span {
                            @include transform(rotateY(180deg));
                            margin-right: 0;
                        }
                        .titlelist {
                            height: 93px;
                            overflow: hidden;
                        }
                    }
                }
            }
        }
    }
}
