.pricingpage {
    --wCol1: 290px;
    --wColBox: 246px;
    --pdTableRow: 25px;
    --borderColor: #e1e8f1;
    .scpricing {
        position: relative;
        min-height: initial;
        .container {
            @media (min-width: 992px) {
                max-width: 1060px;
            }
        }
        &:before {
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            position: absolute;
            z-index: 0;
            $bg-color: #081130;
            $dot-color: #111a69;
            $dot-size: 2px;
            $dot-space: 38px;
            background: linear-gradient(90deg, $bg-color ($dot-space - $dot-size), transparent 1%) center,
                linear-gradient($bg-color ($dot-space - $dot-size), transparent 1%) center, $dot-color;
            background-size: $dot-space $dot-space;
            opacity: 1;
            pointer-events: none;
        }
        .scmain__textbox {
            .swipebox {
                color: $text-white-cl;
                font-family: psb;
                font-size: $fs-text-ss;
                text-transform: uppercase;
                display: inline-flex;
                margin-top: 20px;
                margin-bottom: 20px;
                &__text {
                    position: relative;
                    cursor: pointer;
                    opacity: 0.4;
                    @include transition($t);
                    .saleoff {
                        @include mid(true);
                        position: absolute;
                        right: -78px;
                        top: 0;
                        background: linear-gradient(
                            313.04deg,
                            rgba(54, 120, 247, 0.2) 3.19%,
                            rgba(167, 112, 239, 0.2) 100%,
                            rgba(167, 112, 239, 0.2) 100%
                        );
                        font-family: psb;
                        border-radius: 30px;
                        padding: 1px 10px;
                        span {
                            @include gradient;
                        }
                    }

                    &.--text2 {
                        opacity: 1;
                        @include transition($t);
                    }
                }
                &__btn {
                    width: 44px;
                    height: 26px;
                    margin: 0 16px;
                    border-radius: 30px;
                    border: 2px solid rgba(255, 255, 255, 0.2);
                    position: relative;
                    cursor: pointer;
                    .dot {
                        @include size(18px);
                        top: 50%;
                        @include transform(translateY(-50%) translateX(0));
                        position: absolute;
                        @include gradient;
                        @include br(50%);
                        @include transition($t);
                    }
                }
                &.active {
                    .swipebox__text {
                        opacity: 1;
                        @include transition($t);
                        &.--text1 {
                            opacity: 1;
                            @include transition($t);
                        }
                        &.--text2 {
                            opacity: 0.4;
                            @include transition($t);
                        }
                    }
                    .swipebox__btn {
                        .dot {
                            @include transform(translateY(-50%) translateX(-100%));
                            @include transition($t);
                        }
                    }
                }
                @include xs {
                    &__text {
                        display: flex;
                        .saleoff {
                            position: relative;
                            right: 0;
                            top: 0;
                            margin-left: 5px;
                        }
                    }
                    &__btn {
                        margin: 0 8px;
                    }
                }
            }
        }
        .scpricing__table-wrap {
            position: relative;
            .pin-spacer {
                width: auto !important;
                height: auto !important;
                padding: 0 !important;
                margin: 33px 0 0 var(--wCol1) !important;
                z-index: 500 !important;
            }
        }
        &__boxs {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(3, var(--wColBox));
            margin-left: var(--wCol1);
            margin-top: 50px;
            &-item {
                padding: 34px 28px 36px 28px;
                background: $main-bg-cl;
                border-left: 1px solid var(--borderColor);
                box-sizing: border-box;
                &:last-child {
                    width: calc(var(--wColBox) + 2px);
                    border-right: 1px solid var(--borderColor);
                }
                i {
                    height: 32px;
                    display: block;
                    margin-bottom: 6px;
                    img {
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .des {
                    height: 70px;
                    margin: 2px 0;
                    overflow: hidden;
                    font-size: $fs-text-ss;
                    line-height: $lh-text-ss;
                }
                .price {
                    color: #848898;
                    margin-bottom: 20px;
                    font-size: $fs-text-ss;
                    line-height: $lh-text-ss;
                    strong {
                        color: $text-cl;
                    }
                    .title {
                        color: $text-greydark-cl;
                        margin: 0 3px;
                        position: relative;
                        bottom: -1px;
                        font-family: psb;
                    }
                }
            }
        }
        &__table {
            position: relative;
            z-index: 10;
            .table {
                background: $white-cl;
                border-left: 1px solid var(--borderColor);
                border-right: 1px solid var(--borderColor);
                .tbhead {
                    width: 100%;
                    height: 54px;
                    background: #f4f8fd;
                    display: flex;
                    align-items: center;
                    padding: 0 var(--pdTableRow);
                    border-bottom: 1px solid var(--borderColor);
                }
                .tbcontent {
                    border-bottom: 1px solid var(--borderColor);
                    .tr {
                        display: grid;
                        grid-template-columns: var(--wCol1) repeat(3, 1fr);
                        .td {
                            display: flex;
                            align-items: center;
                            padding: 20px var(--pdTableRow);
                            font-size: $fs-text-s;
                            strong {
                                margin-right: 0.5rem;
                            }
                            &:not(:first-child) {
                                text-align: center;
                                justify-content: center;
                            }
                            &:not(:last-child) {
                                border-right: 1px solid var(--borderColor);
                            }
                        }
                    }
                }
            }
        }
        &__tablemobile {
            margin-left: 15px;
            display: none;
            .table {
                display: grid;
                grid-template-columns: var(--wCol1) repeat(3, 1fr);
                .item {
                    border-left: var(--borderButton) solid $border-blue-cl;
                    border-right: var(--borderButton) solid $border-blue-cl;
                    border-bottom: var(--borderButton) solid $border-blue-cl;
                    border-bottom-right-radius: 5px;
                    border-bottom-left-radius: 5px;
                    ul {
                        margin-bottom: 15px;
                        li {
                            display: flex;
                            align-items: center;
                            padding: 8px 15px;
                            font-size: $fs-text-s;
                            &.head {
                                width: 100%;
                                height: 54px;
                                background: #f4f8fd;
                                display: flex;
                                align-items: center;
                                padding: 0 var(--pdTableRow);
                                font-family: psb;
                                margin-bottom: 10px;
                            }
                            &:not(:first-child) {
                                &.head {
                                    margin-top: 8px;
                                }
                            }
                            strong {
                                margin-right: 0.5rem;
                            }
                            i {
                                width: 20px;
                                height: 30px;
                                flex-shrink: 0;
                                @include mid(true);
                                margin-right: 10px;
                            }
                        }
                    }
                    .btn {
                        margin: 15px;
                    }
                }
            }
        }
        &__bottombox {
            margin-top: 100px;
            padding: 20px 35px;
            background: $main-bg-cl;
            @include br(10px);
            border: 1px solid #e6e4fc;
            display: flex;
            align-items: center;
            margin-bottom: -35px;
            z-index: 10;
            position: relative;
            &-content {
                margin-left: 30px;
                h5 {
                    font-size: 1.8rem;
                    line-height: 2.6rem;
                    font-weight: 400;
                    margin-bottom: 5px;
                    font-family: psb;
                }
                p {
                    a {
                        color: $blue-cl;
                    }
                }
            }
            @include xs {
                flex-direction: column;
                text-align: center;
                margin-bottom: 50px;
                margin-top: 50px;
                img {
                    margin-bottom: 15px;
                }
                &-content {
                    margin-left: 0;
                }
            }
        }
        .bgwhite {
            background: #fff;
            width: 100%;
            height: calc(100% - 692px);
            position: absolute;
            bottom: 0;
            left: 0;
        }
        @include sm {
            .scmain__textbox {
                text-align: center;
                .title {
                    max-width: 260px;
                    margin-left: auto;
                    margin-right: auto;
                }
            }

            .scpricing__table-wrap {
                overflow-x: scroll;
                margin-left: -15px;
                margin-right: -15px;
            }
            &__boxs {
                margin-top: 35px;
                grid-template-columns: repeat(3, 260px);
                column-gap: 15px;
                margin-left: 15px;
                padding: 0;
                background: none;
                &-item {
                    border-left: var(--borderButton) solid $border-blue-cl;
                    border-right: var(--borderButton) solid $border-blue-cl;
                    border-top: var(--borderButton) solid $border-blue-cl;
                    border-top-right-radius: 5px;
                    border-top-left-radius: 5px;
                    padding: 25px 15px;
                }
            }
            &__table {
                margin-left: 15px;
                display: none;
                .table {
                    .tbhead {
                        display: none;
                    }
                    .tbcontent {
                        .tr {
                            grid-template-columns: repeat(3, 260px);
                            column-gap: 15px;
                            .td {
                                background: $main-bg-cl;
                                &:first-child {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
            &__tablemobile {
                display: block;
                .table {
                    margin-right: 15px;
                    column-gap: 15px;
                    grid-template-columns: repeat(3, 260px);
                }
            }
            .bgwhite {
                height: calc(100% - 580px);
            }
        }
    }
    .sctestimonials {
        padding: 0;
        position: relative;
        z-index: 1;
        &.--style2 {
            color: $text-cl;
            .sctestimonials__slider {
                padding-top: 100px;
                background: $blue-bg-cl;
                .slide {
                    color: $text-cl;
                    .slide__top {
                        .slide__top-title {
                            .title {
                                color: $text-cl;
                            }
                        }
                        .slide__top-user {
                            .controls {
                                display: flex;
                                align-items: center;
                                &__btn {
                                    img,
                                    svg {
                                        opacity: 0.3;
                                        @include transition($t);
                                        * {
                                            fill: $text-cl;
                                        }
                                    }
                                    &:first-child {
                                        img,
                                        svg {
                                            @include transform(rotate(180deg) translateX(2px));
                                        }
                                    }
                                    &::after {
                                        background: $bluedark-bg-cl;
                                    }
                                    &:hover {
                                        &::after {
                                            opacity: 0.1;
                                            @include transition($t);
                                        }
                                        img,
                                        svg {
                                            opacity: 1;
                                            @include transition($t);
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .slide__timeline {
                        ul {
                            li {
                                background: rgba(2, 120, 255, 0.2);
                            }
                        }
                    }
                    .slide__bottom {
                        .slide__bottom-title {
                            .titlelist__item {
                                color: $text-cl;
                            }
                        }
                    }
                }
            }
        }
        @include sm {
            background: $blue-bg-cl;
            padding: 50px 0;
            &.--style2 {
                .sctestimonials__slider {
                    padding-top: 0;
                    .slide {
                        .slide__top {
                            .slide__top-user {
                                .controls {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .scfaq {
        padding: 150px 0 80px 0;
        .container {
            display: grid;
            grid-template-columns: 260px 1fr;
            column-gap: 100px;
        }
        &__textbox {
            &-des {
                margin-bottom: 8px;
            }
        }
        &__list {
            .accordion {
                &__content {
                    border-bottom: 1px solid #e1e8f2;
                    padding-left: 25px;
                    &-title {
                        padding: 18px 18px 18px 0px;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        position: relative;
                        text-transform: none;
                        cursor: pointer;
                        color: #3a4159;
                        font-size: $fs-text;
                        line-height: $lh-text;
                        font-family: psb;
                        @include transition($t);
                        i {
                            position: absolute;
                            top: 16px;
                            left: -25px;
                            @include transform(rotateX(180deg));
                            @include transition($t);
                            svg * {
                                fill: #3a4159;
                            }
                        }
                        &:hover {
                            opacity: 0.6;
                            @include transition($t);
                        }
                    }
                    &-text {
                        padding: 0 0px 18px 0px;
                        font-size: $fs-text-s;
                        display: none;
                        strong {
                            margin-bottom: 5px;
                        }
                        &.--transparent {
                            background: none;
                            padding: 0;
                        }
                    }
                    &.active {
                        .accordion__content-title {
                            color: $text-blue-cl;
                            @include transition($t);
                            i {
                                top: 20px;
                                @include transform(rotateX(0deg));
                                @include transition($t);
                                svg * {
                                    fill: $text-blue-cl;
                                    @include transition($t);
                                }
                            }
                            &:hover {
                                opacity: 1;
                            }
                        }
                    }
                    &:first-child {
                        .accordion__content-text {
                            display: block;
                        }
                    }
                }
                @include xs {
                    &__content {
                        &-text {
                            &.--transparent {
                                padding: 0;
                            }
                            .item {
                                padding: 15px;
                                p {
                                    &:first-child {
                                        i {
                                            width: 30px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        @include xs {
            padding: 50px 0;
            .container {
                grid-template-columns: 1fr;
                column-gap: 0px;
            }
            &__textbox {
                margin-bottom: 30px;
            }
        }
    }
    .sccta {
        margin-bottom: 40px;
        &__box {
            background: $black-bg-cl;
            padding: 70px;
            text-align: center;
            color: $text-white-cl;
            @include br(10px);
            .title {
                margin-top: 0;
                margin-bottom: 10px;
            }
            .btn {
                max-width: 180px;
                margin: 32px auto 0 auto;
            }
        }
        @include xs {
            background: $black-bg-cl;
            &__box {
                padding: 40px;
            }
        }
    }
}
