.policypage {
    .footer {
        .container {
            padding-top: 80px;
            border-top: 1px solid #e1e8f1;
        }
    }
}
.scpolicy {
    padding-bottom: 70px;
    &:after {
        content: "";
        width: 100%;
        height: 80%;
        background: rgb(255, 255, 255);
        background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
        bottom: -2px;
        left: 0;
        position: absolute;
        z-index: 2;
    }
    .textbox {
        text-align: center;
        margin-bottom: 70px;
    }
    &__content {
        border: 1px solid #e1e8f1;
        border-radius: 10px;
        padding: 70px 100px;
        background-color: $main-bg-cl;
        .title {
            margin: 30px 0 10px 0;
        }
        p {
            margin-bottom: 20px;
        }
    }
    @include xs {
        .textbox {
            margin-bottom: 35px;
        }
        &__content {
            padding: 15px;
            .title {
                margin: 10px 0 10px 0;
            }
            p {
                margin-bottom: 20px;
            }
        }
    }
}
