.footer {
    padding: $pd-section 0;
    .container {
        display: flex;
    }
    &__logo {
        margin-right: 100px;
        img {
            width: 168px;
        }
    }
    &__main {
        flex: 1;
        &-title {
            margin-top: 0;
            margin-bottom: 40px;
            max-width: 500px;
            &.title {
                font-family: psb;
            }
        }
        &-network {
            display: flex;
            align-items: center;
            margin-bottom: 63px;
            a {
                display: flex;
                align-items: center;
                font-family: pr;
                i {
                    display: inline-block;
                    margin-right: 15px;
                    margin-bottom: -2px;
                }
                &:not(:last-child) {
                    margin-right: 85px;
                }
            }
        }
        &-nav {
            display: flex;
            align-items: center;
            li {
                a {
                    display: inline-block;
                    color: $text-blue-cl;
                    font-family: pm;
                    position: relative;
                    padding: 1px 0;
                    @include transition($t);
                    &:after {
                        content: "";
                        height: 6px;
                        width: 6px;
                        position: absolute;
                        right: -12px;
                        top: 4px;
                        border-radius: 50%;
                        background: $orange-bg-cl;
                        display: none;
                    }
                    &.--circle {
                        &:after {
                            display: block;
                        }
                    }
                }
                &:not(:last-child) {
                    margin-right: 40px;
                }
            }
        }
    }
    &__bottom {
        width: 100%;
        padding-top: 30px;
        margin-top: 30px;
        border-top: 1px solid #e7e8eb;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: $fs-text-ss;
        color: $text-grey-cl;
        ul {
            display: flex;
            li {
                &:not(:last-child) {
                    margin-right: 30px;
                }
                a {
                    font-size: $fs-text-ss;
                    color: $text-grey-cl;
                    @include transition($t);
                    &:hover {
                        opacity: 0.6;
                        @include transition($t);
                    }
                }
            }
        }
    }
    @include sm {
        &__logo {
            margin-right: 80px;
        }
        &__main {
            a {
                &:not(:last-child) {
                    margin-right: 40px;
                }
            }
        }
    }
    @include xs {
        padding: calc(#{$pd-section} - 30px) 0;
        .container {
            flex-direction: column;
            text-align: center;
        }
        &__logo {
            margin-right: 0;
            img {
                width: 60px;
            }
        }
        &__main {
            &-title {
                max-width: 300px;
                margin: 15px auto;
            }
            &-network {
                flex-direction: column;
                margin-bottom: 28px;
                a {
                    &:not(:last-child) {
                        margin: 2px 0;
                    }
                    i {
                        display: none;
                    }
                }
            }
            &-nav {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                li {
                    text-align: center;
                    &:not(:last-child) {
                        margin: 5px 0;
                    }
                }
            }
        }
        &__bottom {
            flex-direction: column;
            padding-top: 15px;
            margin-top: 15px;
        }
    }
}
