.header {
    width: 100%;
    max-width: $container-max-width;
    padding: 30px 0;
    position: fixed;
    top: 0;
    @include transform(translate(0%, 0%));
    z-index: 500;
    @include transition($t);
    &.--fixed {
        @include transform(translate(0%, -100%));
        @include transition($t);
    }
    &.--hidden {
        @include transform(translate(0%, -100%));
        @include transition($t);
    }
    &.--bgwhite {
        padding: 8px 0;
        background: $white-cl;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
        @include transition($t);
    }
    .container-fluid {
        width: 100%;
        max-width: 1920px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__logo {
        display: flex;
        align-items: center;
        outline: 0;
        svg,
        img {
            width: 40px;
        }
        .title {
            color: $text-cl;
            margin-bottom: -2px;
            margin-left: 10px;
        }
    }
    &__nav {
        @include mid(true);
        position: absolute;
        left: 50%;
        @include transform(translateX(-50%));
        li {
            a {
                color: $text-blue-cl;
                padding: 3px 0;
                margin: 0 24px;
                font-family: pm;
                @include transition($t);
                position: relative;
            }
        }

    }
    &__login {
        .btngroup {
            .btn {
                width: auto;
            }
        }
    }
    &__btnmenu {
        width: 24px;
        height: 22px;
        position: relative;
        display: none;
        span {
            width: 100%;
            height: 3px;
            border-radius: 40px;
            background: rgb(41, 52, 159);
            position: absolute;
            background: linear-gradient(347deg, rgba(41, 52, 159, 1) 0%, rgba(167, 112, 239, 1) 56%);
            &:first-child {
                top: 0;
            }
            &:nth-child(2) {
                width: 60%;
                top: 50%;
                @include transform(translateY(-50%));
            }
            &:last-child {
                bottom: 0;
            }
        }
    }
    &.--style2 {
        .header__logo {
            .title {
                color: $text-white-cl;
                @include sm {
                    color: $text-cl;
                }
            }
        }
        .header__nav {
            li {
                a {
                    color: $text-white-cl;
                    &:before {
                        background: $main-bg-cl;
                    }
                }
            }
        }
        .header__login {
            .btngroup {
                .btn {
                    a {
                        color: $text-white-cl;
                    }
                }
            }
        }
        &.--bgwhite {
            .header__logo {
                .title {
                    color: $text-cl;
                }
            }
            .header__nav {
                li {
                    a {
                        color: $text-blue-cl;
                        &:before {
                            background: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);
                        }
                    }
                }
            }
            .header__login {
                .btngroup {
                    .btn {
                        a {
                            color: $text-white-cl;
                        }
                        &.--transparent {
                            a {
                                color: $text-blue-cl;
                            }
                        }
                    }
                }
            }
        }
    }
    @include sm {
        position: sticky;
        &__nav {
            display: none;
            &.--termsPolicy {
                display: flex;
            }
        }
        &__login {
            display: none;
        }
        &__btnmenu {
            display: block;
        }
    }
    @include xs {
        padding: 12px 0;
        background: $main-bg-cl;
        box-shadow: inset 0px -1px 0px #f0f0f0;
    }
}
