/*primary colors*/
$blue-cl: #142b6f;
$white-cl: #fff;
$black-cl: #000;
$purple-cl: #a770ef;

/*global text color*/
$text-cl: #121212;
$text-white-cl: #fff;
$text-grey-cl: #595959;
$text-greydark-cl: #242f4b;
$text-blue-cl: #29349f;
$text-bluedark-cl: #091130;
$text-gradient-cl: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);

/*global bg color*/
$main-bg-cl: #fff;
$black-bg-cl: #121212;
$blue-bg-cl: #f2f8ff;
$bluedark-bg-cl: #0278ff;
$orange-bg-cl: #ff4444;
$gradient-bg-cl: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);
$gradientdark-bg-cl: linear-gradient(313.04deg, #0245c5 3.19%, #a770ef 100%, #6500ea 100%);
$green-bg: rgba(0, 200, 81, 0.18);

// border
$border-blue-cl: #dde7ff;

// timing transition
$t: 0.3s;
$cubic: cubic-bezier(0.84, 0.13, 0.33, 0.97);

/*font families*/
$font-stack: "Open Sans", sans-serif;
$font-main: pr;

/*headings typo*/
$fs-text: 1.6rem;
$lh-text: 2.4rem;

$fs-text-s: 1.4rem;
$lh-text-s: 2.4rem;

$fs-text-ss: 1.2rem;
$lh-text-ss: 1.8rem;

$pd-section: 70px;

//============================================================//

/*BOXED LAYOUT*/
$container-max-width: 100%;
$container-width: 1300px;

/*GRID - media queries breakpoints*/
$xxs-min: 576px;
$xs-min: 768px;
$sm-min: 992px;
$md-min: 1200px;
$lg-min: 1600px;

$xxs-max: ($xxs-min - 1); // Small Mobile
$xs-max: ($xs-min - 1); // Mobile
$sm-max: ($sm-min - 1); // Tablet
$md-max: ($md-min - 1); // Desktop
$lg-max: ($lg-min - 1); // Large Desktop
