// css button, title, icon, elements...
img {
    max-width: 100%;
}
a {
    text-decoration: none;
}
strong {
    font-family: psb;
}

//================================== BUTTON ===========================//

.btn {
    @include br(5px);
    overflow: hidden;
    white-space: nowrap;
    @include transition($t);
    position: relative;
    background: $gradient-bg-cl;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
        width: 100%;
        height: var(--heightButton);
        padding: 10px 20px;
        line-height: initial;
        display: block;
        overflow: hidden;
        color: $white-cl;
        font-family: pm;
        text-align: center;
        @include mid(true);
        position: relative;
        z-index: 10;
    }
    &:after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: $gradientdark-bg-cl;
        opacity: 0;
        @include transition($t);
    }
    &.--transparent {
        border: none;
        background: none;
        @include br(none);
        a {
            background: none;
            color: $text-blue-cl;
        }
        &:after {
            display: none;
        }
    }
    &.--white {
        border: var(--borderButton) solid #dde7ff;
        a {
            height: calc(var(--heightButton) - (var(--borderButton) * 2));
            background: $main-bg-cl;
            color: $text-blue-cl;
            @include transition($t);
            &:hover {
                background: #f6f1fd;
                @include transition($t);
            }
        }
        &.--noborder {
            border: 0;
            a {
                height: var(--heightButton);
            }
        }
    }
    &.--big {
        a {
            //height: calc(var(--heightButton) + 10px);
            height: var(--heightButton);
            padding: 10px 24px;
            font-size: $fs-text;
        }
        &.--white {
            a {
                height: calc(var(--heightButton) + 10px - (var(--borderButton) * 2));
            }
        }
    }
    &:hover {
        &:after {
            opacity: 1;
            @include transition($t);
        }
    }
    @include xs {
        &.--big {
            a {
                //height: calc(var(--heightButton) + 6px);
                height: calc(var(--heightButton) + 6px);
            }
            &.--white {
                a {
                    height: calc(var(--heightButton) + 6px - (var(--borderButton) * 2));
                }
            }
        }
    }
}
.btnbox {
    width: 214px;
    margin: 0 auto;
    @include br(8px);
    border: 3px solid $border-blue-cl;
    overflow: hidden;
    &__inner {
        background: $white-cl;
        width: 100%;
        height: 50px;
        display: inline-flex;
        align-items: center;
        a {
            flex: 1;
            height: 100%;
            @include mid(true);
            font-family: pm;
            &.active {
                height: 40px;
                background: $bluedark-bg-cl;
                color: $text-white-cl;
                border-radius: 5px;
                margin: 5px 5px 5px 0;
            }
        }
    }
}

//================================== TITLE ===========================//

.label {
    font-size: 1.8rem;
    line-height: 2.6rem;
    font-family: pm;
    background: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    @include xs {
        font-size: 1.4rem;
        line-height: 2.2rem;
    }
}

.title {
    font-family: pb;
    font-size: 4rem;
    line-height: 5.6rem;
    color: $text-bluedark-cl;
    margin: 12px 0 20px 0;
    &.--white {
        color: $white-cl;
    }
    &.--black {
        color: $text-cl;
    }
    &.--t0 {
        font-size: 6.4rem;
        line-height: 7.2rem;
        font-family: pb;
    }
    &.--t1 {
        font-size: 4.8rem;
        line-height: 5.6rem;
        font-family: psb;
    }
    &.--t2 {
        font-size: 4.8rem;
        line-height: 5.2rem;
        font-family: pb;
        letter-spacing: -0.04em;
    }
    &.--t3 {
        font-size: 4rem;
        line-height: 5rem;
        font-family: pb;
    }
    &.--t4 {
        font-size: 2.6rem;
        line-height: 3.8rem;
        font-family: psb;
    }
    &.--t5 {
        font-size: 2.2rem;
        line-height: 3rem;
        font-family: psb;
        margin: 0;
    }
    &.--gradient {
        display: table;
        background: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    @include xs {
        &.--t0 {
            font-size: 4.8rem;
            line-height: 5.6rem;
        }
        &.--t1 {
            font-size: 3.2rem;
            line-height: 4rem;
        }
        &.--t2 {
            font-size: 3.3rem;
            line-height: 4.4rem;
        }
    }
}

.--text-s {
    font-size: $fs-text-s;
    line-height: $lh-text-s;
}

.--text-ss {
    font-size: $fs-text-ss;
    line-height: $lh-text-ss;
}

.--blue-cl {
    color: $text-blue-cl;
}

.--hover-line {
    position: relative;
    &:before {
        content: "";
        width: 100%;
        height: 2px;
        background: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);
        position: absolute;
        bottom: 0;
        left: 50%;
        display: block;
        @include transform(translateX(-50%) scaleX(0));
        @include transform-origin(right);
        @include transition(transform $t cubic-bezier(0.68, 0.21, 0.16, 0.94));
    }
    &:hover,
    &.active {
        &:before {
            @include transform(translateX(-50%) scaleX(1));
            @include transform-origin(left);
            @include transition(transform $t cubic-bezier(0.68, 0.21, 0.16, 0.94));
        }
    }
}
.check {
    .text {
        padding-left: 30px;
    }
    .checkcontainer {
        display: table;
        position: relative;
        padding-left: 28px;
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        bottom: -1px;
        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 19px;
            width: 19px;
            z-index: 10;
            left: 0;
            margin: 0;
            &:checked ~ .checkmark {
                background-color: $bluedark-bg-cl;
                border: 1px solid $bluedark-bg-cl;
            }
            &:checked ~ .checkmark:after {
                display: block;
            }
        }

        .checkmark {
            height: 19px;
            width: 19px;
            position: absolute;
            top: 1px;
            left: 0;
            border: 1px solid #b8b8b8;
            border-radius: 3px;
            &:after {
                content: "";
                position: absolute;
                display: none;
            }
            &:after {
                left: 6px;
                top: 0px;
                width: 6px;
                height: 14px;
                border: solid white;
                border-width: 0 2px 2px 0;
                -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                transform: rotate(45deg);
            }
        }
        &:hover input ~ .checkmark {
            border: 1px solid $bluedark-bg-cl;
        }

        input:checked ~ .checkmarkradio {
            background-color: $bluedark-bg-cl;
        }

        input:checked ~ .checkmarkradio:after {
            display: block;
        }
        .checkmarkradio {
            position: absolute;
            top: 2px;
            left: 0;
            height: 22px;
            width: 22px;
            background-color: #eee;
            border-radius: 50%;
            &:after {
                content: "";
                position: absolute;
                display: none;
            }
            &:hover input ~ .checkmarkradio {
                background-color: #ccc;
            }
            &:after {
                top: 8px;
                left: 8px;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background: white;
            }
        }
    }
}
