// css navigator (Menu)
.navmobile {
    @include size(100%);
    position: fixed;
    top: 0;
    left: 0;
    background: $black-bg-cl;
    z-index: 1;
    padding-left: calc(var(--leftWrapper) + 20px);
    overflow-y: scroll;
    display: none;
    &__button {
        width: 30px;
        height: 30px;
        position: absolute;
        top: 30px;
        right: 30px;
        justify-self: flex-end;
        display: flex;
        overflow: hidden;
        z-index: 100;
        span {
            height: 100%;
            width: 3px;
            left: 15px;
            border-radius: 5px;
            background: $main-bg-cl;
            position: absolute;
            @include transition(0.4s cubic-bezier(0.63, 0.16, 0.22, 0.94));
            &:first-child {
                @include transform(translate(30px, 30px) rotate(-45deg));
            }
            &:last-child {
                @include transform(translate(-30px, 30px) rotate(45deg));
                @include transition-delay(0.18s);
            }
        }
        &.active {
            span {
                &:first-child {
                    @include transform(translate(0px, 0px) rotate(-45deg));
                }
                &:last-child {
                    @include transform(translate(0px, 0px) rotate(45deg));
                    @include transition-delay(0.18s);
                }
            }
        }
    }
    &__inner {
        height: 610px;
        padding-top: 70px;
        padding-bottom: 70px;
        position: absolute;
        top: 50%;
        @include transform(translate(20%, -50%));
        @include transition(0.45s cubic-bezier(0.63, 0.16, 0.22, 0.94));
        ul {
            li {
                a {
                    font-size: 3.4rem;
                    line-height: 4.4rem;
                    font-family: psb;
                    color: $text-white-cl;
                    padding: 12px 0;
                    display: block;
                    position: relative;
                    &:before {
                        content: "";
                        width: 20px;
                        height: 20px;
                        background: url('https://demo.bear.plus/commenty/wp-content/themes/commenty_theme/img/arrow-menu.svg') no-repeat center;
                        background-size: contain;
                        position: absolute;
                        top: 50%;
                        left: -34px;
                        @include transform(translateY(-50%));
                        opacity: 0;
                    }
                    &.active,
                    &:hover {
                        @include gradient;
                        &:before {
                            opacity: 1;
                        }
                    }
                }
            }
        }
        .btngroup {
            max-width: 180px;
            margin-top: 60px;
            flex-direction: column;
            .btn {
                &:not(:last-child) {
                    margin-bottom: 15px;
                }
                &:last-child {
                    position: relative;
                    background: linear-gradient(313.04deg, #3678f7 3.19%, #a770ef 100%, #a770ef 100%);
                    &::after {
                        content: "";
                        width: calc(100% - 4px);
                        height: calc(100% - 4px);
                        background: $black-bg-cl;
                        position: absolute;
                        @include mid;
                        border-radius: 3px;
                        z-index: 0;
                        opacity: 1;
                    }
                    a {
                        position: relative;
                        z-index: 10;
                        background: none;
                    }
                }
            }
        }
    }
    &.--show-nav {
        .navmobile__inner {
            @include transform(translate(0%, -50%));
            @include transition(0.45s 0.1s cubic-bezier(0.63, 0.16, 0.22, 0.94));
        }
    }
    @include sm {
        display: block;
    }
}
